
import { store } from '@/store';
import { useRoute, useRouter } from 'vue-router';
import { ref } from 'vue';

import subpage from '@/components/subpage.vue';
import imageDisplayContainer from '@/components/image-component.vue';

import { images as portfolioImages, imageDetails } from '@/store/portfolio';

export default {
  components: { subpage, imageDisplayContainer },
  setup() {
    const route = useRoute();
    const router = useRouter();

    type galleryType = 'all' | 'advertisement' | 'faces' | 'dance' | 'nudes' | 'fashion';
    type galleryContent = { preview: string; full: string; ratio: number };
    let gallery: {
      advertisement: galleryContent[];
      faces: galleryContent[];
      dance: galleryContent[];
      nudes: galleryContent[];
      fashion: galleryContent[];
    } = {
      advertisement: [],
      faces: [],
      dance: [],
      nudes: [],
      fashion: [],
    };

    function parseImages(imageDetailsList: imageDetails[]): galleryContent[] {
      const result: galleryContent[] = [];
      //fill gallery
      for (const galleryImage of imageDetailsList) {
        let resultEntry: any = {};
        resultEntry.full = require(`../../public/images/portfolio/${galleryImage.fullsizePath}`);
        resultEntry.preview = require(`../../public/images/portfolio/${galleryImage.previewPath}`);
        resultEntry.ratio = galleryImage.width / galleryImage.height;
        result.push(resultEntry);
      }
      return result;
    }

    gallery.advertisement = parseImages(portfolioImages.advertisement);
    gallery.dance = parseImages(portfolioImages.dance);
    gallery.faces = parseImages(portfolioImages.faces);
    gallery.fashion = parseImages(portfolioImages.fashion);
    gallery.nudes = parseImages(portfolioImages.nudes);

    const currentGalleryName = ref<galleryType>('all');
    const currentGalleryImages = ref<{ type: galleryType; image: galleryContent }[]>([]);

    addFromGallery('advertisement', currentGalleryImages.value);
    addFromGallery('faces', currentGalleryImages.value);
    addFromGallery('dance', currentGalleryImages.value);
    addFromGallery('nudes', currentGalleryImages.value);
    addFromGallery('fashion', currentGalleryImages.value);

    function setGallery(value: galleryType) {
      currentGalleryName.value = value;
      router.push({ name: route.name!, query: { view: value } });
      removeWhereNot(value, currentGalleryImages.value);
      if (value == 'all') {
        addFromGallery('advertisement', currentGalleryImages.value);
        addFromGallery('faces', currentGalleryImages.value);
        addFromGallery('dance', currentGalleryImages.value);
        addFromGallery('nudes', currentGalleryImages.value);
        addFromGallery('fashion', currentGalleryImages.value);
      }
      if (value == 'advertisement') {
        addFromGallery('advertisement', currentGalleryImages.value);
      }
      if (value == 'faces') {
        addFromGallery('faces', currentGalleryImages.value);
      }
      if (value == 'dance') {
        addFromGallery('dance', currentGalleryImages.value);
      }
      if (value == 'nudes') {
        addFromGallery('faces', currentGalleryImages.value);
      }
      if (value == 'fashion') {
        addFromGallery('fashion', currentGalleryImages.value);
      }
    }

    function addFromGallery(
      value: galleryType,
      current: { type: galleryType; image: galleryContent }[]
    ): void {
      for (const iterator of gallery[value] as galleryContent[]) {
        if (
          !current.some((element) => {
            if (element.image.full === iterator.full) {
              return true;
            }

            return false;
          })
        ) {
          current.push({ type: value, image: iterator });
        }
      }
    }

    function removeWhereNot(
      value: galleryType,
      current: { type: galleryType; image: galleryContent }[]
    ): void {
      const indexToRemove: number[] = [];
      for (let index = current.length - 1; index >= 0; index--) {
        const element = current[index];
        if (element.type !== value) {
          indexToRemove.push(index);
        }
      }
      for (let index = 0; index < indexToRemove.length; index++) {
        const element = indexToRemove[index];
        current.splice(element, 1);
      }
    }

    function onBeforeLeaveTransition(element: Element) {
      const element2 = element as HTMLElement;
      //   console.log(element2);
      element2.style.height = element.clientHeight + 'px';
      element2.style.width = element.clientWidth + 'px';
      //   console.log(element.getBoundingClientRect());
      //   console.log(element.getBoundingClientRect());

      //   element2.style.top = '';
      //   element2.style.left = '';
    }

    function onLeaveTransition(element: Element) {
      const element2 = element as HTMLElement;
      //   console.log(element2);
      //   console.log(element.getBoundingClientRect());

      //   element2.style.top = element.getBoundingClientRect().top + 'px';
      //   element2.style.left = element.getBoundingClientRect().left + 'px';
    }

    function onAfterEnter(element: Element) {
      //   const element2 = element as HTMLElement;
      //   console.log(element.getBoundingClientRect());
      //   element2.style.top = element.getBoundingClientRect().top + 'px';
      //   element2.style.left = element.getBoundingClientRect().left + 'px';
    }

    return {
      onAfterEnter,
      onBeforeLeaveTransition,
      onLeaveTransition,
      store,
      gallery: {
        currentGalleryName,
        currentGalleryImages,
      },
      functions: {
        setGallery,
      },
    };
  },
};
